import React, { useState } from "react";
import { Input } from "antd";

interface IPropsFloatInput {
  label?: string;
  value?: any;
  placeholder?: string;
  type?: string;
  required?: boolean;
  onChange?: () => void;
  name?: string;
}

const FloatInput = (props: IPropsFloatInput) => {
  const [focus, setFocus] = useState(false);
  let { label, value, placeholder, type, required } = props;

  if (!placeholder) placeholder = label;

  const isOccupied = focus || (value && value.length !== 0);

  const labelAsLabel = "top-[-8px] py-[4px] ml-[-4px]";
  const labelAsPlaceHolder = "text-red-100";
  const labelClass = isOccupied ? labelAsLabel : labelAsPlaceHolder;

  const requiredMark = required ? <span className="text-danger">*</span> : null;

  return (
    <div
      className="float-label relative bg-white-100"
      onBlur={() => setFocus(false)}
      onFocus={() => setFocus(true)}
    >
      <Input
        onChange={props.onChange}
        type={type}
        defaultValue={value}
        className="pt-[20px] h-[48px] rounded-[4px] border-gray-300 z-10 bg-transparent t-14-normal text-gray-400"
      />
      <label
        className={
          "top-[7px] ml-[-4px] left-[16px] absolute t-12-semibold leading-[18px] text-gray-600"
        }
      >
        {label} {requiredMark}
      </label>
      {!isOccupied && (
        <label
          className={
            "top-[26px] ml-[-4px] left-[16px] absolute t-14-normal text-gray-400"
          }
        >
          {placeholder}
        </label>
      )}
    </div>
  );
};

export default FloatInput;
